/* eslint-disable no-console */
define(["qlik"], function (qlik) {
  return {
    getVariables:  function () {
      return new Promise(async function (resolve, reject) {
        const app = await qlik.currApp(this);
        const variableProps = {
          qVariableListDef: {
            qType: 'variable'
          }
        };
        const variableList = await app.createGenericObject(variableProps);
        const finalVariableList = variableList.map(function(item){
          return {
            value: item.qName,
            label: item.qName
          };
        });
        resolve(finalVariableList);
      });
    }
  };
});
